* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  text-align: center;
  color: #333;
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.input-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.timer {
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  margin-right: 10px;
  font-family: monospace;
  font-size: 36px;
  display: flex;
}

.value {
  width: 60px;
  height: 60px;
  text-align: center;
  color: #fff;
  background-color: #333;
  border-radius: 10px;
  line-height: 60px;
  display: block;
}

.label {
  text-transform: uppercase;
  margin-top: 10px;
  font-size: 14px;
}

.timer-controls {
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

.start-btn:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

.form {
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

label {
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
  display: flex;
}

input {
  text-align: center;
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover {
  background-color: #0069d9;
}

